import React from 'react';
import PropTypes from 'prop-types';

import { CommonForm } from 'components';

import PopUpLayout from '../popUpLayout/PopUpLayout';

const CommonPopUp = ({
  id, // id    ,        , , id='register-button'
  avatar, //          ProfilePopUp,       
  title, //  , ,  title='Recovery password'
  children, //    ,    /  <GoogleAuth />    CommonPopUp
  onChange, //  ,     
  onClose, //   ,   
  onSubmit, //  
  hasIcon, //     hasICon={false}
  inputs, //  '    inputs={inputs},    .     
  initialValues, //    import { POP_UP_INITIAL_VALUES } from 'constants';        ', , const { AuthValues } = POP_UP_INITIAL_VALUES;
  validationSchema, //   helpers/validationSchemas
  buttonText, //  ,   ,   
  variantMarginTop, //    PrimaryButton,      variant="formPopUp". Margin-top    -,    
  variantIcon, //  
  customInputProps, //  ,    -, 
  settings, //     BoardSettings  CardSettings
  google, //      GoogleAuth
  destination, //   -  authForm -  ,   popup
  variantMessage, //  "authForm"        
  variantMarginBottom, //    title  
}) => {
  return (
    <>
      {destination !== 'authForm' ? (
        <PopUpLayout title={title} handleClose={onClose}>
          <CommonForm
            initialValues={initialValues}
            onSubmit={onSubmit}
            validationSchema={validationSchema}
            avatar={avatar}
            inputs={inputs}
            onChange={onChange}
            customInputProps={customInputProps}
            settings={settings}
            id={id}
            variantIcon={variantIcon}
            hasIcon={hasIcon}
            variantMarginTop={variantMarginTop}
            buttonText={buttonText}
            google={google}
            children={children}
            variantMessage={variantMessage}
            variantMarginBottom={variantMarginBottom}
          />
        </PopUpLayout>
      ) : (
        <CommonForm
          initialValues={initialValues}
          onSubmit={onSubmit}
          validationSchema={validationSchema}
          avatar={avatar}
          inputs={inputs}
          onChange={onChange}
          customInputProps={customInputProps}
          settings={settings}
          id={id}
          variantIcon={variantIcon}
          variantMarginTop={variantMarginTop}
          buttonText={buttonText}
          google={google}
          children={children}
          variantMessage={variantMessage}
        />
      )}
    </>
  );
};

CommonPopUp.propTypes = {
  id: PropTypes.string,
  title: PropTypes.string,
  children: PropTypes.node,
  onClose: PropTypes.func,
  onChange: PropTypes.func,
  onSubmit: PropTypes.func.isRequired,
  hasIcon: PropTypes.bool,
  inputs: PropTypes.arrayOf(
    PropTypes.shape({
      name: PropTypes.string.isRequired,
      type: PropTypes.string.isRequired,
      placeholder: PropTypes.string.isRequired,
      multiline: PropTypes.bool,
    })
  ).isRequired,
  initialValues: PropTypes.objectOf(PropTypes.string).isRequired,
  validationSchema: PropTypes.object,
  buttonText: PropTypes.string.isRequired,
  variantMarginTop: PropTypes.string,
  variantIcon: PropTypes.string,
  avatar: PropTypes.bool,
  settings: PropTypes.bool,
  google: PropTypes.bool,
  destination: PropTypes.string,
  variantMessage: PropTypes.string,
  variantMarginBottom: PropTypes.string,
};

export default CommonPopUp;
